import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
import cuDate from '@/components/custom/cu-date.vue'
import cuPopups from '@/components/custom/cu-popups.vue'
import { api } from '@/config/common.js'
import { query } from '@/api/common.js'
import { dateFormat, numberFormat } from '@/utils/tools.js'
export default {
	components: {
		uniList,
		uniListItem,
		cuDate,
		cuPopups
	},
	data() {
		return {
			datas: {},
			title: '销售分析',
			date: '',
			startDate: '',
			endDate: '',
			showFilter: false,
			filterData: [{title:'客户分组'}]
		}
	},
	onLoad(options) {
		this.date = options.date
		this.startDate = options.startDate
		this.endDate = options.endDate
	},
	computed: {
		headerHeight() {
			return this.$headerHeight
		}
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		numberFilter(number) {
			return numberFormat(number)
		},
		handleNavTo(url, params) {
			uni.navigateTo({
				url: url+'?'+params
			})
		},
		getData(startDate, endDate) {
			this.$refs.loading.open()
			let reqData = {
				startDate: startDate,
				endDate: endDate
			}
			query(api.report, reqData).then(res => {
				this.$refs.loading.close()
				if (res.status == 200 && res.data.returnCode == '0000') {
					this.datas = res.data.data
				}
			}).catch(error => {
				this.$refs.loading.close()
			})
		},
		handleDateChange(val) {
			this.date = val
			let sDate = new Date()
			let eDate = new Date()
			switch(val) {
				case 'day':
					this.startDate = dateFormat('YYYY-mm-dd', sDate) + ' 00:00:00'
					this.endDate = dateFormat('YYYY-mm-dd', eDate) + ' 23:59:59'
					break
				case 'yesterday':
					sDate.setDate(sDate.getDate() - 1)
					this.startDate = dateFormat('YYYY-mm-dd', sDate) + ' 00:00:00'
					eDate.setDate(eDate.getDate() - 1)
					this.endDate = dateFormat('YYYY-mm-dd', eDate) + ' 23:59:59'
					break
				case 'week':
					let w = sDate.getDay()
					w = w == 0 ? 6 : w - 1
					sDate.setDate(sDate.getDate() - w)
					this.startDate = dateFormat('YYYY-mm-dd', sDate) + ' 00:00:00'
					this.endDate = dateFormat('YYYY-mm-dd', eDate) + ' 23:59:59'
					break
				case 'month': 
					sDate.setDate(1)
					this.startDate = dateFormat('YYYY-mm-dd', sDate) + ' 00:00:00'
					this.endDate = dateFormat('YYYY-mm-dd', eDate) + ' 23:59:59'
					break
				case 'year':
					sDate.setMonth(0)
					sDate.setDate(1)
					this.startDate = dateFormat('YYYY-mm-dd', sDate) + ' 00:00:00'
					this.endDate = dateFormat('YYYY-mm-dd', eDate) + ' 23:59:59'
					break
			}
			this.getData(this.startDate, this.endDate)
		},
		handleShowPopDate() {
			this.date = 'custom'
			this.$refs.popDate.open()
		},
		handleDateCustomChange(val) {
			this.startDate = val.startDate + ' 00:00:00'
			this.endDate = val.endDate + ' 23:59:59'
			this.getData(this.startDate, this.endDate)
		},
		handleClickFilter() {
			this.showFilter = !this.showFilter
			console.log("###")
		}
	}
}
